
<template>
  <div id="map-container"></div>
</template>

<script>
import loadBMap from '@/utils/loadBMap'

export default {
  name: 'position-cascader',
  props: {
    locationPoint: Object,
    flag: {
      type: Boolean,
      default: () => true
    },
    fenceRadius:
    {
      type: Number,
      default: 600
    },
    fencePoint: {
      type: Array,
      default: () =>
        [121.840164, 31.880629]}
  },
  watch: {
    locationPoint (newVal, oldVal) {
      this.renderBMap()
    }
  },
  // mounted () {
  //   this.locationPoint = {
  //     Longitude: 121.8333,
  //     Latitude: 31.8444
  //   }
  // },
  methods: {
    renderBMap () {
      loadBMap()
        .then((e) => {
          // /* global BMAP_ANIMATION_BOUNCE */

          const BMap = e || undefined
          const map = new BMap.Map('map-container', { enableMapClick: false })
          const dataObj = this.locationPoint
          const point = new BMap.Point(dataObj.Longitude, dataObj.Latitude)
          const marker = new BMap.Marker(point)
          const opts = {
            width: 250,
            height: 100,
            title: `<div style='font-size:1.2rem;font-weight:600'>${dataObj.DisplayName}</div>`,
            offset: new BMap.Size(0, -15)
          }
          const infoWindow = new BMap.InfoWindow(
            `<el-card class="box-card">
              <div  class="text item">
                  经度：${dataObj.Longitude}
              </div>
              <div  class="text item">
                  维度：${dataObj.Latitude}
              </div>
              <div  class="text item">
                  ${dataObj.InstallationSite}
              </div>
            </el-card>
            `,
            opts
          )
          const markerOut = new BMap.Marker(121.844689662539, 31.8968051081232)
          map.addOverlay(markerOut)
          map.addOverlay(marker)
          map.enableScrollWheelZoom()
          map.centerAndZoom(point, 15)
          // marker.setAnimation(BMAP_ANIMATION_BOUNCE)
          marker.addEventListener(`click`, () => {
            map.openInfoWindow(infoWindow, point) // 打开信息窗口
          })

          // 如果有电子围栏，需要渲染出来
          if (this.flag) {
            const fencePoint = new BMap.Point(
              this.fencePoint[0],
              this.fencePoint[1]
            )

            const circleOpt = {
              opt: {
                strokeColor: 'tomato',
                strokeWeight: 2,
                strokeOpacity: 0.6,
                fillOpacity: 0.6,
                fillColor: 'white'
              }
            }

            const circle = new BMap.Circle(
              // point,
              fencePoint,
              this.fenceRadius,
              circleOpt.opt
            )

            map.addOverlay(circle)
            // 添加旗帜覆盖物
            if (!fencePoint.equals(point)) {
              const flagIcon = new BMap.Icon(
                require(`@/assets/img/screenData/flag.png`),
                new BMap.Size(16, 16)
              )
              const marker = new BMap.Marker(fencePoint, { icon: flagIcon })
              map.addOverlay(marker)
            }
          }
        })
        .catch((err) => {
          alert(err)
        })
    }
  }
}
</script>

<style lang="less" scoped>
#map-container {
  width: 100%;
  height: 100%;
}
</style>



