<!--
 * @Author: Demon3443002624@qq.com
 * @Date: 2021-11-11 17:06:01
 * @Description: 垂直的进度条
-->
<template>
  <div id="Progressvertical">
    <div class="containbox">
      <div class="Progressvertical">
        <el-row>
          <el-col :span="2">L1</el-col>
          <el-col :span="18">
            <el-progress
              :format="format"
              :stroke-width="12"
              color="#6a1a97"
              :percentage="
                realTimeData.L1 ? (realTimeData.L1.Value * 100) / max : 0
              "
            ></el-progress>
          </el-col>
          <el-col :span="4">{{
            realTimeData.L1 ? realTimeData.L1.Value + realTimeData.L1.Unit : ""
          }}</el-col>
        </el-row>
      </div>
      <div class="Progressvertical">
        <el-row>
          <el-col :span="2">L2</el-col>
          <el-col :span="18">
            <el-progress
              :format="format"
              color="#6a1a97"
              :stroke-width="12"
              :percentage="
                realTimeData.L2 ? (realTimeData.L2.Value * 100) / max : 0
              "
            ></el-progress>
          </el-col>
          <el-col :span="4">{{
            realTimeData.L2 ? realTimeData.L2.Value + realTimeData.L2.Unit : ""
          }}</el-col>
        </el-row>
      </div>
      <div class="Progressvertical">
        <el-row>
          <el-col :span="2">L3</el-col>
          <el-col :span="18">
            <el-progress
              :format="format"
              color="#6a1a97"
              :stroke-width="12"
              :percentage="
                realTimeData.L3 ? (realTimeData.L3.Value * 100) / max : 0
              "
            ></el-progress>
          </el-col>
          <el-col :span="4">{{
            realTimeData.L3 ? realTimeData.L3.Value + realTimeData.L3.Unit : ""
          }}</el-col>
        </el-row>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Progressvertical",
  data() {
    return {
      // 电流最大值
      max: 1000,
    };
  },
  props: {
    msg: {
      type: String,
      default: "lail",
    },
    realTimeData: Object,
  },
  computed: {},
  methods: {
    format(per) {
      return "";
    },
  },
};
</script>
<!-- <style lang="less" scoped> 此处设置 scoped 后设置 element 样式 el-progress-bar 会失效-->
<style lang="less" scoped>
#Progressvertical {
  display: flex;
  align-items: center;
  width: 220px;
  height: 220px;
  transform: rotateZ(-90deg) translateY(-17px);
  .Progressvertical {
    margin: 0 0 30px 0;
    width: 200px;
    div[class*="el-col-2"],
    div[class~="el-col-4"] {
      transform: rotateZ(90deg);
    }
  }
}
</style>
<style lang="less">
// 重写 ElementUI 进度条样式，曲线两边的间距
#Progressvertical {
  .el-progress-bar {
    padding-right: 0;
    margin-right: 0;
  }
}
</style>



