<!--
 * @Author: Demon3443002624@qq.com
 * @Date: 2021-11-11 17:02:46
 * @Description: 客户首页-自适应布局
-->
<template>
  <div
    id="screen-data"
    v-loading="loading"
    element-loading-text="数据检索中，请您耐心等待..."
    element-loading-background="rgba(0, 0, 0, 0.6)"
  >
    <div class="bg-box">
      <img src="@/assets/img/screenData/u1.png" width="100%" height="100%" />
    </div>
    <el-row class="screen-data-head" type="flex" align="middle">
      <img src="@/assets/img/screenData/u4.svg" width="100%" height="100%" />
      <el-col :span="4">
        <el-row>
          <el-col class="city-box" :span="8">
            <el-popover
              placement="top-start"
              width="200"
              trigger="click"
              popper-class="screen-data-myPopover"
            >
              <div>
                <el-row v-show="changePositionFlag == true">
                  <el-col :span="18"> {{ crewPosition }} </el-col>
                  <el-col class="change-location" :span="5" :offset="1">
                    <div @click="changePositionFlagFunc">[点击切换]</div>
                  </el-col>
                </el-row>
                <el-row v-show="changePositionFlag == false">
                  <position-cascader
                    placeholder="请选择机组所在位置"
                    @positionChange="positionChange"
                  />
                  <button class="sure" @click="selectPosition">确定</button>
                  <button class="cancel" @click="changePositionFlagFunc">取消</button>
                </el-row>
              </div>
              <el-button slot="reference">
                <i class="el-icon-location-information"> </i>
                {{ simplePosition }}
              </el-button>
            </el-popover>
          </el-col>
          <el-col :span="16">
            <div class="screen-search-box">
              <el-row type="flex" align="middle">
                <el-col :span="24">
                  <el-autocomplete
                    v-model="mchNameForQuery"
                    placeholder="按机组编号查询"
                    :fetch-suggestions="querySearchAsync"
                    :maxlength="10"
                    :trigger-on-focus="false"
                    @select="handleSelect"
                  ></el-autocomplete>
                </el-col>
              </el-row>
            </div>
          </el-col>
        </el-row>
      </el-col>
      <el-col :span="4">
        <div>
          <el-select v-model="mchCode" placeholder="">
            <el-option
              v-for="item in dropdownOptions_mch"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </div>
      </el-col>
      <el-col :span="8">
        <el-row>
          <el-col>
            <el-row type="flex" justify="center">
              <div class="title">FPSS租赁云平台</div>
            </el-row>
            <el-row class="nowtime little" type="flex" justify="center"> </el-row>
          </el-col>
        </el-row>
      </el-col>
      <el-col :span="6">
        <el-row>
          <el-col :span="12">
            <el-row type="flex" align="middle">
              <el-avatar :src="HeadImgUrl"> </el-avatar>
              &nbsp;&nbsp;工程师，您好！
            </el-row>
          </el-col>
          <el-col :span="6">
            <el-row>
              <el-col :span="12">
                <el-button type="success " class="goto-system-btn enter" @click="pushManage"
                  >进入系统</el-button
                >
              </el-col>
              <el-col :span="12">
                <el-button type="success " class="goto-system-btn logout" @click="logout"
                  >退出登录</el-button
                >
              </el-col>
            </el-row>
          </el-col>
        </el-row>
      </el-col>
      <el-col :span="2">
        <div>
          <el-select v-model="value2" placeholder="请选择">
            <el-option
              v-for="item in options_supportdropdown"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </div>
      </el-col>
    </el-row>
    <el-row class="screen-data-warning">
      <img class="img-leftbar" src="@/assets/img/screenData/u23.svg" />
      <img class="img-rightbar" src="@/assets/img/screenData/u23.svg" />
      <img class="img-left" src="@/assets/img/screenData/u18.svg" />
      <div class="warning-box">
        <img class="img-middle" src="@/assets/img/screenData/u411.svg" />
      </div>
      <div class="warning-box">
        <el-carousel
          height="48px"
          :interval="3000"
          direction="vertical"
          :autoplay="true"
          @click="warningClickHandler"
        >
          <el-carousel-item v-for="(item, index) in warningdatas.d1" :key="'warningKey' + index">
            <el-row align="middle" justify="center" type="flex">
              <img class="img-warning" src="@/assets/img/screenData/Warning-dataScreen.svg" />
              {{ item }}
            </el-row>
          </el-carousel-item>
          <el-carousel-item v-for="(item, index) in warningdatas.d2" :key="'warningKey2' + index">
            <el-row align="middle" justify="center" type="flex">
              <img class="img-warning" src="@/assets/img/screenData/Repair.svg" />
              {{ item }}
            </el-row>
          </el-carousel-item>
        </el-carousel>
      </div>
      <div class="warning-box btn">
        <TabledialogWarning
          :warningDataTable="warningDataTable"
          :keepDataTable="keepDataTable"
          @changeMchID="changeMchID"
        />
      </div>
      <img class="img-right" src="@/assets/img/screenData/u21.svg" />
    </el-row>
    <el-row class="screen-data-content">
      <el-col :span="8" class="content-left">
        <div class="top">
          <el-row class="box-load">
            <el-col :span="7">
              <div class="box-title">◉ 机组负载数据</div>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="9" :offset="3">
              <div class="box-progress">
                <el-progress
                  type="dashboard"
                  :width="85"
                  linecap="butt"
                  :stroke-width="10"
                  :percentage="realTimeData.COS ? (realTimeData.COS.Value * 100) / powerMax : 0"
                  color="#203aa3"
                  :format="format1"
                  >功率因数</el-progress
                >
                <span>{{
                  realTimeData.COS ? realTimeData.COS.Value + realTimeData.COS.Unit : ''
                }}</span>
              </div>
            </el-col>
            <el-col :span="10">
              <div class="box-progress">
                <el-progress
                  type="dashboard"
                  :stroke-width="10"
                  linecap="butt"
                  :width="85"
                  :percentage="realTimeData.PW ? (realTimeData.PW.Value * 100) / pwerMax : 0"
                  :format="format2"
                  color="#af620f"
                  >负载功率</el-progress
                >
                <span>{{
                  realTimeData.PW ? realTimeData.PW.Value + realTimeData.PW.Unit : ''
                }}</span>
              </div>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="9" :offset="3">
              <div class="box-progress-vertical">
                <Progressvertical :realTimeData="realTimeData" />
              </div>
            </el-col>
            <div class="line-vertical"></div>
            <el-col :span="12">
              <div class="load-right-box">
                <div class="up">
                  <div class="title">相电压</div>
                  <div class="chart">
                    <el-row>
                      <el-col :span="4"> L1-N</el-col>
                      <el-col :span="16" :pull="1">
                        <el-progress
                          :format="formatnull"
                          :stroke-width="11"
                          color="#b28f13"
                          :percentage="
                            realTimeData.L1N ? (realTimeData.L1N.Value * 100) / voltageMax : 0
                          "
                        ></el-progress>
                      </el-col>
                      <el-col :span="3">{{
                        realTimeData.L1N ? realTimeData.L1N.Value + realTimeData.L1N.Unit : ''
                      }}</el-col>
                    </el-row>
                    <el-row>
                      <el-col :span="4"> L2-N</el-col>
                      <el-col :span="16" :pull="1">
                        <el-progress
                          :format="formatnull"
                          :stroke-width="11"
                          :percentage="
                            realTimeData.L2N ? (realTimeData.L2N.Value * 100) / voltageMax : 0
                          "
                          color="#b28f13"
                        ></el-progress>
                      </el-col>
                      <el-col :span="3">{{
                        realTimeData.L2N ? realTimeData.L2N.Value + realTimeData.L2N.Unit : ''
                      }}</el-col>
                    </el-row>
                    <el-row>
                      <el-col :span="4"> L3-N</el-col>
                      <el-col :span="16" :pull="1">
                        <el-progress
                          :format="formatnull"
                          :stroke-width="11"
                          color="#b28f13"
                          :percentage="
                            realTimeData.L3N ? (realTimeData.L3N.Value * 100) / voltageMax : 0
                          "
                        ></el-progress>
                      </el-col>
                      <el-col :span="3">{{
                        realTimeData.L3N ? realTimeData.L3N.Value + realTimeData.L3N.Unit : ''
                      }}</el-col>
                    </el-row>
                  </div>
                </div>
                <div class="line-vertical"></div>
                <div class="down">
                  <div class="title">
                    线电压（{{ realTimeData.LHz ? realTimeData.LHz.Value + `Hz` : '' }}）
                  </div>
                  <div class="chart">
                    <el-row>
                      <el-col :span="4"> L1-L2</el-col>
                      <el-col :span="16" :pull="1">
                        <el-progress
                          :format="formatnull"
                          :stroke-width="11"
                          color="#b28f13"
                          :percentage="
                            realTimeData.L12 ? (realTimeData.L12.Value * 100) / electricMax : 0
                          "
                        ></el-progress>
                      </el-col>
                      <el-col :span="3">{{
                        realTimeData.L12 ? realTimeData.L12.Value + realTimeData.L12.Unit : ''
                      }}</el-col>
                    </el-row>
                    <el-row>
                      <el-col :span="4"> L2-L3</el-col>
                      <el-col :span="16" :pull="1">
                        <el-progress
                          :format="formatnull"
                          :stroke-width="11"
                          color="#b28f13"
                          :percentage="
                            realTimeData.L23 ? (realTimeData.L23.Value * 100) / electricMax : 0
                          "
                        ></el-progress>
                      </el-col>
                      <el-col :span="3">{{
                        realTimeData.L23 ? realTimeData.L23.Value + realTimeData.L23.Unit : ''
                      }}</el-col>
                    </el-row>
                    <el-row>
                      <el-col :span="4"> L3-L1</el-col>
                      <el-col :span="16" :pull="1">
                        <el-progress
                          :format="formatnull"
                          :stroke-width="11"
                          color="#b28f13"
                          :percentage="
                            realTimeData.L31 ? (realTimeData.L31.Value * 100) / electricMax : 0
                          "
                        ></el-progress>
                      </el-col>
                      <el-col :span="3">{{
                        realTimeData.L31 ? realTimeData.L31.Value + realTimeData.L31.Unit : ''
                      }}</el-col>
                    </el-row>
                  </div>
                </div>
              </div>
            </el-col>
          </el-row>
        </div>
        <div class="line-horizontal"></div>
        <div class="bottom">
          <el-row>
            <div class="box-title">◉ 机组视频监控</div>
          </el-row>
          <el-row class="myvideo">
            <!-- autoplay="autoplay" -->
            <!-- muted 属性如果不添加，视频无法自动播放！-->
            <!-- id="myPlayer" muted="muted" loop="loop" -->
            <video
              width="100%"
              height="100%"
              controls="controls"
              ref="myVideo"
              autoplay="autoplay"
              muted="muted"
            ></video>
          </el-row>
          <!-- <div>
            <img class="img-leftbar" src="@/assets/img/screenData/u25p000.svg" />
          </div> -->
        </div>
      </el-col>
      <el-col :span="8" class="content-middle">
        <el-row class="top" type="flex" justify="center">
          <div class="fueltitle">机组油耗总计数据</div>
        </el-row>
        <el-row type="flex" align="bottom" litte>
          <Tabledialog :mchCode="mchCode.toString()" />
        </el-row>
        <el-row class="middle1" type="flex" justify="center">
          <el-col :span="12">
            <el-row type="flex" justify="end">
              <div>
                <el-row class="titletext" type="flex" opacity justify="end">总发电量</el-row>
                <el-row class="valuetext" type="flex" large justify="end">{{
                  TotalElectricalEnergy
                }}</el-row>
              </div>
            </el-row>
          </el-col>
          <div class="line-horizontal"></div>
          <el-col :span="12">
            <el-row type="flex" justify="start">
              <div>
                <el-row type="flex" class="titletext" opacity justify="start">总油耗</el-row>
                <el-row type="flex" class="valuetext" large justify="start">{{ TotalFuel }}</el-row>
              </div>
            </el-row>
          </el-col>
        </el-row>
        <el-row class="middle2" type="flex" justify="center">
          <FuelComponent :displayName="displayName" :status="status" @changeSubMch="changeSubMch" />
        </el-row>
        <el-row class="bottom" type="flex" justify="center">
          <div class="icon-container">
            <svg-icon slot="prefix" icon-class="WaterTemperature" />
            <icon-rotating />
            <div class="text-container">
              <div>水温</div>
              <div>
                {{ realTimeData.WATER ? realTimeData.WATER.Value + realTimeData.WATER.Unit : '' }}
              </div>
            </div>
          </div>
          <div class="icon-container">
            <svg-icon slot="prefix" icon-class="OilPressure" />
            <icon-rotating />
            <div class="text-container text2">
              <div>油压</div>
              <div>
                {{ realTimeData.FUEL ? realTimeData.FUEL.Value + realTimeData.FUEL.Unit : '' }}
              </div>
            </div>
          </div>
          <div class="icon-container">
            <svg-icon slot="prefix" icon-class="rotatingr" />
            <icon-rotating />
            <div class="text-container text3">
              <div>转速</div>
              <div>
                {{
                  realTimeData.ROTATE ? realTimeData.ROTATE.Value + realTimeData.ROTATE.Unit : ''
                }}
              </div>
            </div>
          </div>
        </el-row>
      </el-col>
      <el-col :span="8" class="content-right">
        <el-row class="right-top">
          <div class="box-title">◉ 机组位置信息</div>
          <el-button class="change-btn" v-show="changeLocationFlag" @click="changeLocation"
            >切换</el-button
          >
          <MapComponent
            v-show="!showLocation"
            :mapdata="mapdata"
            @cb="mapcb"
            :documentWidth="documentWidth"
          />
          <div v-show="showLocation" class="location-box">
            <location ref="changeLocationRender" :locationPoint="locationPoint">
              location
            </location>
          </div>
        </el-row>
        <div class="line-horizontal"></div>
        <el-row class="right-bottom">
          <div class="box-title">◉ 剩余燃油曲线</div>
          <FuelCurveComponent :fuelcurve="fuelcurve" />
        </el-row>
      </el-col>
    </el-row>
  </div>
</template>
<script>
// 引入组件
import Progressvertical from './components/Progressvertical'
import FuelComponent from './components/FuelComponent'
import MapComponent from './components/MapComponentBmap'
import FuelCurveComponent from './components/FuelCurveComponent'
// import IconRotating from './components/Iconrotating'
import IconRotating from './components/Iconrotating/indexDraw.vue'
import Tabledialog from './components/Tabledialog'
import TabledialogWarning from './components/TabledialogWarning'
import PositionCascader from '@/components/PositionCascader'
import Location from '@/components/Location'

import Hls from 'hls.js'
// 引入测试数据
import data from './data'
// 引入公共方法
import { getWeekTime, formatTime } from '@/utils/common'
import { mapActions, mapGetters } from 'vuex'

// 引入萤石云视频
// import EZUIKit from "ezuikit-js";
import '@/utils/ezuikit'
// 定义状态枚举
export default {
  name: 'CustomerMonitoringScreen',
  components: {
    Progressvertical,
    FuelComponent,
    MapComponent,
    FuelCurveComponent,
    IconRotating,
    Tabledialog,
    TabledialogWarning,
    PositionCascader,
    Location
  },
  data() {
    return {
      // 机组编号（全局生效）
      mchCode: '',
      mchInfo: {},
      // 搜索的机组名
      // 20211125周四11:35:17lpeng：之前是查询机组编码，改为机组名称
      mchNameForQuery: '',
      // 实时报警数据
      warningdatas: {},
      warningDataTable: [],
      // 保养数据
      keepDataTable: [],
      // 所有机组信息
      mchlist: [],
      state: ['离线', '停机', '警告', '报警', '运行'],
      timeout: null,
      searchMchcode: '',
      dropdownOptions_mch: [],
      options_supportdropdown: data.options_support,
      value2: '001',
      mapdata: {},
      displayName: '',
      status: '',
      // 总发电量
      TotalElectricalEnergy: '',
      // 总油耗
      TotalFuel: '',
      // 实时数据
      realTimeData: {},
      // 自适应布局 datav 的scale布局
      width: '',
      timer: null,
      // screenHeight: document.getElementById("screen-data").clientHeight||0,
      // screenWidth: document.getElementById("screen-data").clientWidth||0,
      // screenWidth2: document.getElementById("screen-data").clientWidth||0,
      documentWidth: document.documentElement.clientWidth,
      // 燃油曲线数据
      fuelcurve: {},
      // 功率因数最大值
      powerMax: 1,
      // 负载功率最大值
      pwerMax: 50,
      // 相电压最大值
      voltageMax: 500,
      // 线电压最大值
      electricMax: 500,
      timer01: null,
      timer02: null,
      // 实时视频地址
      // VideoH5Url:`@/assets/videos/fc.mp4`,
      VideoH5Url: ``,
      player: null,
      crewPosition: '',
      selectCrewPosition: '',
      simplePosition: '',
      changePositionFlag: true,
      loading: false,
      // 20211213周一8:58:42lpeng：显示定位还是地图
      showLocation: false,
      // showLocation: true,
      // 当前机组坐标
      locationPoint: null,
      // 页面加载完成后才显示，切换按钮，不然点击后无法加载地图
      changeLocationFlag: false,
      HeadImgUrl: null
      // HeadImgUrl: 'https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png'
    }
  },
  watch: {
    mchCode(n, old) {
      // this.mchNameForQuery = this.mchCode.toString();
      this.setMchInfo(this.mchCode)
      this.freshDataOnce()
    }
  },
  mounted() {
    // 等比例自适应布局
    window.onresize = () => {
      this.documentWidth = document.documentElement.clientWidth
      const windowWidth = window.screen.width
      let scale = windowWidth === 0 ? 1 : this.documentWidth / 1920
      const screenDataPage = document.getElementById('screen-data')
      screenDataPage && (screenDataPage.style.transform = 'scale(' + scale + ')')
    }
    // 执行一下窗口resize
    window.onresize()
    // 渲染时间控件（每秒更新）
    this.renderTime()
    // 获取所有机组信息
    this.getAllMchInfo()
    // 获取报警保养数据（全部）
    this.setWarnKeepData()
    // 获取用户头像
    this.getAccount()
    // 定时刷新数据
    this.freshData()
  },
  beforeDestroy() {
    // 销毁定时器
    clearInterval(this.timer01)
    clearInterval(this.timer02)
  },
  computed: {
    ...mapGetters(['selectCrew'])
  },
  methods: {
    // 开启实时数据刷新（轮询）
    freshData() {
      this.timer01 = setInterval(() => {
        this.setMchInfo(this.mchCode)
      }, 30000)
      this.timer02 = setInterval(() => {
        this.setWarnKeepData()
      }, 1000 * 60 * 60)
    },
    // 仅在机组被更改后执行一次的操作
    freshDataOnce() {
      // 1.本机组定位
      this.$api.screenData.getsingleMchInfo({ id: this.mchCode }).then((resOriginal) => {
        const res = resOriginal.Data
        this.locationPoint = res
      })
      // 2.本机组的监控视频,获取萤石云监控视频
      this.$api.screenData.FindGeneratorSetByMonitor({ id: this.mchCode }).then((resOriginal) => {
        const res = resOriginal.Data
        this.VideoH5Url = res && res.VideoH5Url ? res.VideoH5Url : ''
        this.renderVideo()
      })
    },
    getAccount() {
      this.$api.account.findAccount().then((res) => {
        if (res.IsSuccess) {
          const data = res.Data
          this.HeadImgUrl = data.HeadImgUrl
        }
      })
    },
    // 切换当前机组定位和机组分布地图
    changeLocation(event) {
      this.showLocation = !this.showLocation
      if (this.showLocation) {
        this.$refs.changeLocationRender.renderBMap()
      }
    },
    // 切换分机组
    changeSubMch(a, b, id) {
      // 根据机组编码切换机组
      // this.setMchInfo(id)
    },
    async logout() {
      const res = await this.$api.login.logout()
      if (res.IsSuccess) {
        this.$wsCache.delete('loginFlag')
        this.$wsCache.delete('userInfo')
        this.$wsCache.delete('selectCrew')
        this.$router.push('/login')
      }
    },
    // 保养列表中更改mchID
    changeMchID(param) {
      const selectId = param && param.mchId
      if (selectId !== this.mchCode) {
        this.mchCode = selectId
        this.$message(`当前设备已变更`)
      }
    },
    // 初始化萤石云视频播放
    renderVideo() {
      // const video = document.getElementById('myPlayer')
      // const src = this.VideoH5Url
      // video.src = src
      // // eslint-disable-next-line no-undef
      // this.player = new EZUIPlayer('myPlayer')
      // setTimeout(() => {
      //   this.player.play()
      // }, 0)
      // 使用 hls 实现
      if (this.VideoH5Url) {
        const hls = new Hls()
        hls.attachMedia(this.$refs.myVideo)
        hls.on(Hls.Events.MEDIA_ATTACHED, (_) => {
          hls.loadSource(this.VideoH5Url)
          hls.on(Hls.Events.MANIFEST_PARSED, function () {
            this.$refs.myVideo.play()
          })
        })
        // 出错处理
        hls.on(Hls.Events.ERROR, function (event, data) {
          if (data.fatal) {
            switch (data.type) {
              case Hls.ErrorTypes.NETWORK_ERROR:
                // try to recover network error
                console.error('fatal network error encountered, try to recover')
                hls.startLoad()
                break
              case Hls.ErrorTypes.MEDIA_ERROR:
                console.error('fatal media error encountered, try to recover')
                hls.recoverMediaError()
                break
              default:
                // cannot recover
                hls.destroy()
                break
            }
          }
        })
      }
    },
    selectHandler(e) {},
    setWarnKeepData() {
      this.$api.screenData.FindGeneratorSetRadioIndexPage().then((resOriginal) => {
        if (resOriginal) {
          const res = resOriginal.Data
          const alarmList = res.AlarmList || []
          const remindList = res.RemindList || []
          const warningData = []
          const keepData = []
          alarmList.forEach((e, i) => {
            const obj = {
              order: i,
              time: formatTime(e.Time).format('yyyy-MM-DD HH:mm:ss'),
              mchname: e.GensetName,
              divisionmch: '测试分机组',
              warningcontent: e.Content,
              warningtype: e.Type,
              operation: '查看'
            }
            warningData.push(obj)
          })
          remindList.forEach((e, i) => {
            const obj = {
              order: i,
              unit: e.GensetName,
              plandate: formatTime(e.Time).format('YYYY-MM-DD'),
              type: e.Type,
              operation: '查看',
              keepContent: e.Content
            }
            keepData.push(obj)
          })
          const maxWordLength = 35
          this.warningdatas = {
            d1: [
              ...warningData.map((item) => {
                let msg = '实时报警：' + item.mchname + '-' + item.warningcontent
                if (msg.length > maxWordLength) {
                  msg = msg.slice(0, maxWordLength) + `...`
                }
                return msg
              })
            ],
            d2: [
              ...keepData.map((item) => {
                let msg = '保养提醒：' + item.unit + item.keepContent
                if (msg.length > maxWordLength) {
                  msg = msg.slice(0, maxWordLength) + `...`
                }
                return msg
              })
            ]
          }
          this.warningDataTable = warningData
          this.keepDataTable = keepData
        }
      })
    },
    // resizePage() {
    //   debugger
    //   const bi = this.screenWidth / (this.screenHeight * 3.1);
    //   const app = document.getElementById("screen-data");
    //   app.style.height = this.screenHeight * 3.1;
    //   app.style.width = this.screenHeight;
    //   app.style.overflow = "hidden";
    //   app.style.transform = "scale(" + bi + ")";
    //   app.style.transformOrigin = "left top 0";
    // },
    ...mapActions(['toggleSelectCrew']),
    warningClickHandler(e) {},
    renderTime() {
      setTimeout(() => {
        document.querySelector('#screen-data .nowtime').innerHTML = getWeekTime()
        this.renderTime()
      }, 1000)
    },
    format1(str) {
      return '功率因数'
    },
    format2(str) {
      return '负载功率'
    },
    formatnull(str) {
      return ''
    },
    querySearchAsync(queryString, cb) {
      const mchlist = this.mchlist
      const results = queryString
        ? mchlist
            .filter((item, index) => {
              return item.DisplayName.toString().includes(queryString)
            })
            .map((v, i) => {
              return {
                value: v.DisplayName,
                ID: v.ID,
                status: this.state[Number(v.Status)],
                displayName: v.DisplayName
              }
            })
        : mchlist
      cb(results)
    },
    mapcb(e) {
      this.displayName = e.displayName
      this.status = e.status
      this.setMchInfo(e.mchCode)
      this.mchCode = e.mchCode
    },
    handleSelect(item) {
      this.mchCode = item.ID
      this.mchNameForQuery = item.ID.toString()
      this.setMchInfo(this.mchCode)
    },
    // 设置单机组详细信息
    setMchInfo(mchCode) {
      this.changePositionFlag = false
      // 获取单个机组详细信息
      this.$api.screenData.getsingleMchInfo({ id: mchCode }).then((resOriginal) => {
        // 渲染单机组数据
        const res = resOriginal.Data
        this.status = res.StatusName
        this.displayName = res.DisplayName
        this.mchNameForQuery = res.DisplayName
        this.locationPoint = res
        this.TotalElectricalEnergy = res.TotalElectricalEnergy
          ? res.TotalElectricalEnergy + 'Kw·h'
          : ''
        if (this.TotalElectricalEnergy) this.TotalFuel = '··'
        else this.TotalFuel = ''
      })
      // 获取实时数据信息
      this.$api.screenData.getsingleMchInfoRealtime({ id: mchCode }).then((resOriginal) => {
        // 20211124 周三 10:18:52 lpeng：此处原为查看当前机组的报警信息，现已更改为查看所有机组的报警/保养信息
        // if (res && res.ShutDownAlarms.length == 0 && res.WarnAlarms == 0) {
        //   this.warningdatas = ["暂无警告信息"];
        // } else {
        //   this.warningdatas = [
        //     ...res.ShutDownAlarms.map(
        //       (item) => "实时报警：" + this.displayName + "-" + item.Describe
        //     ),
        //     ...res.WarnAlarms.map(
        //       (item) => "实时报警：" + this.displayName + "-" + item.Describe
        //     ),
        //   ];
        // }
        // 渲染实时数据信息
        const res = resOriginal.Data
        const nowdata = res.NowData
        const data = {
          // 负载数据
          L1: nowdata.r_025,
          L2: nowdata.r_026,
          L3: nowdata.r_027,
          L1N: nowdata.r_013,
          L2N: nowdata.r_014,
          L3N: nowdata.r_015,
          LHz: nowdata.r_019,
          L12: nowdata.r_010,
          L23: nowdata.r_011,
          L31: nowdata.r_012,
          COS: nowdata.r_044,
          PW: nowdata.r_032,
          WATER: nowdata.r_050,
          FUEL: nowdata.r_051,
          ROTATE: nowdata.r_047
        }
        this.realTimeData = data
        this.changeLocationFlag = true
      })
      const selectInfo = this.mchlist.filter((item) => item.ID === this.mchCode)[0]
      this.toggleSelectCrew(selectInfo)
      this.crewPosition = selectInfo.InstallationSite
      this.simplePosition = this.getCity(selectInfo.InstallationSite)
      // 获取燃油曲线数据
      // 近1小时数据
      const nowTime = this.$moment().format('yyyy-MM-DD HH:mm:ss')
      const hourTime = this.$moment().add('-1', 'h').format('yyyy-MM-DD HH:mm:ss')
      this.$api.screenData
        .FindGeneratorSetHistoryDataV3(
          {
            TimeStart: hourTime,
            TimeEnd: nowTime,
            Items: 'r_052'
          },
          {
            // id: this.mchCode,
            id: 7236
          }
        )
        .then((resOriginal) => {
          let res = resOriginal.Data
          // 测试数据
          // res = data.fuelcurved;
          // 正式数据
          if (res && res.Datas.length > 0) {
            const tmpX = []
            const tmpData = []
            const data = res.Datas[0].Data || []
            data.forEach((e) => {
              const time = formatTime(e.Time).format('yyyy-MM-DD HH:mm:ss')
              // 20211124 周三 15:39:36 lpeng：油量暂无
              const arr = [time, e.Value, '暂无']
              tmpX.push(time)
              tmpData.push(arr)
            })
            // 20211124 周三 15:58:25 lpeng：该数据暂时请求不到
            this.fuelcurve = {
              x: tmpX,
              data: tmpData
            }
          }
        })
    },
    pushManage() {
      this.$router.push('/syshomePage/home')
    },
    async getAllMchInfo() {
      const resOriginal = await this.$api.common.getCrewList(
        {
          DisplayMode: '1'
          // DisplayName:"9995"
        },
        { Page: 1, PageSize: 1999 }
      )
      const res = resOriginal.Data
      // 1.设置所有机组信息
      this.mchlist = res.Data || []
      // 2.设置选择机组下拉菜单的选项数组
      this.dropdownOptions_mch = this.mchlist.map((item) => {
        const obj = {
          value: item.ID,
          label: item.DisplayName
        }
        return obj
      })
      // 2.1 选中第一个
      const mchID = this.selectCrew.ID
      this.mchCode =
        mchID || (this.dropdownOptions_mch.length > 0 ? this.dropdownOptions_mch[0].value : '')
      // 3.渲染地图数据
      const mapdata = {}
      this.mchlist.forEach((item) => {
        mapdata[item.DisplayName] = [
          item.Longitude,
          item.Latitude,
          item.InstallationSite,
          item.DisplayName,
          this.state[Number(item.Status)],
          item.ID
        ]
      })
      this.mapdata = mapdata
      this.displayName =
        this.dropdownOptions_mch.length > 0 ? this.dropdownOptions_mch[0].label : ''
      this.status = this.state[Number(this.mchlist[0].Status)]
      this.setMchInfo(this.mchCode)
    },
    // 地理位置切换
    positionChange(data) {
      this.selectCrewPosition = data.crewPosition
    },
    // 获取地理位置中的市（不稳定）
    getCity(str) {
      const reg = /.+?(省|市|自治区|自治州|县|区)/g
      const regArr = str.match(reg)

      // 如果找不到市，显示全部
      const resOriginal = !regArr || regArr.length < 2 ? str : regArr[1]
      // 最多显示三个字
      const res = resOriginal.length > 3 ? resOriginal.slice(0, 3) + `...` : resOriginal
      return res
    },
    changePositionFlagFunc() {
      this.changePositionFlag = !this.changePositionFlag
    },
    selectPosition() {
      this.loading = true
      const params = {
        model: {
          DisplayMode: 1, // 1为所有启用，0为所有机组 2为所有禁用
          InstallationSite: this.selectCrewPosition
        }
      }
      this.$api.common
        .getCrewList(params, {
          // IsWeb: true,
          page: 1,
          pageSize: 999
        })
        .then((resOriginal) => {
          const data = resOriginal.Data
          // 如果未搜索到设备列表，则提示用户并不需要更新页面的用户列表
          if (!data || !data.Data || data.Data.length < 1) {
            this.$message('未检索出设备列表！')
            this.loading = false
          } else {
            // 1.更新设备列表
            this.mchlist = data.Data
            // 2.更新下拉列表
            this.dropdownOptions_mch = this.mchlist.map((item) => {
              const obj = {
                value: item.ID,
                label: item.DisplayName
              }
              return obj
            })
            // 3.更新搜索下拉列表--搜索的mchlist
            // 4.更新地图数据
            const mapdata = {}
            this.mchlist.forEach((item) => {
              mapdata[item.DisplayName] = [
                item.Longitude,
                item.Latitude,
                item.InstallationSite,
                item.DisplayName,
                this.state[Number(item.Status)],
                item.ID
              ]
            })
            this.mapdata = mapdata
            // 5.选中第一个
            this.mchCode =
              this.dropdownOptions_mch.length > 0 ? this.dropdownOptions_mch[0].value : ''
            this.loading = false
            // 更新位置
            this.crewPosition =
              this.dropdownOptions_mch.length > 0
                ? this.dropdownOptions_mch[0].InstallationSite
                : ''
            this.$message(`设备列表检索成功，已更新位置为${this.selectCrewPosition}`)
          }
        })
    }
  }
}
</script>
<style lang="less">
// // 自适应
@screen-width: 1920px;
@screen-height: 1080px;
// @screen-width: 3840px;
// @screen-height: 2169px;
// 顶部的文字大小（比正文大一点）
@head-fontsize: 1.2rem;
#screen-data {
  font-family: 'Helvetica Neue', Helvetica, 'PingFang SC', 'Hiragino Sans GB', 'Microsoft YaHei',
    '微软雅黑', Arial, sans-serif;
  color: white;
  width: @screen-width;
  height: @screen-height;
  // transform: scale(0.5);
  transform-origin: left top;
  overflow: hidden;
  min-width: @screen-width;
  // 浅色字体（全局）
  div[opacity] {
    opacity: 0.6;
    color: #b4e4ff;
  }
  // 分隔的虚线的样式（全局）
  div[class*='line-'] {
    border: 1px dotted #1878d1;
  }
  // 背景
  .bg-box {
    position: absolute;
    top: 0;
    left: 0;
    width: @screen-width;
    height: @screen-height;
    z-index: -1;
  }
  // 顶部
  .screen-data-head {
    height: 80px;
    margin: 0 0 5px 0;
    position: relative;
    color: #1acdfd;
    // 重写element输入框的样式
    .el-input__inner {
      background-color: transparent;
      border: 0;
      color: #1acdfd;
      font-size: @head-fontsize;
    }
    .el-col:nth-child(2) {
      .screen-search-box {
        // background-color: black;
        height: 35px;
        width: 95%;
        margin-left: 2%;
        border: 1px solid;
      }
      .el-icon-search {
        cursor: pointer;
      }
    }
    .el-col:nth-child(5) {
      font-size: @head-fontsize;
    }
    .el-col:nth-child(6) > div {
      width: 130px;
    }
    img {
      position: absolute;
    }
    .title {
      font-size: 2.6rem;
      font-weight: bolder;
    }
    .little {
      font-size: 0.8rem;
    }
    .city-box {
      button {
        border: 0;
        background-color: transparent;
        color: white;
        font-size: 1.3rem;
      }
    }
    .goto-system-btn {
      background-color: transparent;
      border: 1px solid #1acdfd;
      padding: 12px 20px;
    }
    .enter:hover {
      background-color: rgba(26, 205, 253, 0.8);
      animation: oto-system-btn 0.5s linear 1;
    }
    .logout {
      transform: translateX(60px);
    }
    .logout:hover {
      background-color: rgba(253, 79, 26, 0.8);
      border: 1px solid rgba(253, 79, 26, 0.8);
      animation: oto-system-btn2 0.5s linear 1;
    }
    @keyframes oto-system-btn {
      0% {
        background-color: rgba(26, 205, 253, 0.1);
      }
      25% {
        background-color: rgba(26, 205, 253, 0.3);
      }
      50% {
        background-color: rgba(26, 205, 253, 0.5);
      }
      75% {
        background-color: rgba(26, 205, 253, 0.6);
      }
      100% {
        background-color: rgba(26, 205, 253, 0.8);
      }
    }
    @keyframes oto-system-btn2 {
      0% {
        background-color: rgba(253, 79, 26, 0.1);
      }
      50% {
        background-color: rgba(253, 79, 26, 0.5);
      }
      100% {
        background-color: rgba(253, 79, 26, 0.8);
      }
    }
  }
  // 警告栏
  .screen-data-warning {
    height: 50px;
    position: relative;
    .img-leftbar {
      position: absolute;
      left: 0;
      top: -4px;
    }
    .img-rightbar {
      position: absolute;
      top: -4px;
      right: 0;
    }
    .warning-box {
      position: absolute;
      height: 48px;
      width: 680px;
      overflow: hidden;
      left: 32%;
      top: 0;
      font-size: 1.3rem;
      .el-row {
        height: 100%;
      }
      .img-warning {
        width: 40px;
        margin: 0 5px 0 10px;
      }
      // 隐藏触发器
      ul {
        visibility: hidden;
      }
    }
    .warning-box[class*='btn'] {
      z-index: 2;
    }
    .img-right {
      position: absolute;
      right: 0;
    }
  }
  // 左侧
  .content-left {
    // opacity: 0.6;
    position: relative;
    .el-progress__text {
      color: white;
    }
    .top {
      height: 380px;
      .el-row {
        margin: 0 0 20px 0;
      }
    }
    .line-vertical {
      position: absolute;
      width: 0;
      height: 240px;
      left: 45%;
    }
    .line-horizontal {
      position: absolute;
      height: 0;
      width: 100%;
      top: 440px;
    }
    .load-right-box {
      height: 240px;
      .up,
      .down {
        height: 45%;
        margin-top: 2%;
      }
      .line-vertical {
        height: 0;
        width: 55%;
      }
      .chart > div {
        margin: 7px 0 0 0;
        height: 20px;
      }
      .el-progress-bar {
        padding: 0;
      }
    }
    .bottom {
      height: 500px;
      padding: 80px 0 0 0;
      .myvideo {
        padding-left: 3%;
      }
    }
  }
  // 中间
  .content-middle {
    position: relative;
    .top {
      .fueltitle {
        background-color: #1374ce;
        height: 42px;
        line-height: 40px;
        border-radius: 0 0 30px 30px;
        width: 30%;
        font-size: 1.2rem;
        text-align: center;
      }
    }
    .middle1 {
      height: 50px;
      margin: 20px 0 0 0;
      .titletext {
        font-size: 1.8rem;
        margin: 0 0 12px 0;
      }
      .valuetext {
        font-size: 2.5rem;
        font-weight: bolder;
      }
      .line-horizontal {
        height: 70px;
        margin: 0 25px;
      }
    }
    .middle2 {
      margin: 80px 0 0 0;
      height: 550px;
    }
    div[litte] {
      font-size: 0.6rem;
      position: absolute;
      top: 14px;
      right: 23%;
      cursor: pointer;
      color: #169bd5;
    }
    div[large] {
      font-size: 1.3rem;
    }
    @content-middle-height: 50px;
    .middle1 {
      .el-divider {
        opacity: 0.4;
        height: @content-middle-height;
      }
    }
    .bottom {
      height: 100px;
      .icon-container {
        position: relative;
        margin: 0 50px;
        @icon-size: 47px;
        .svg-icon {
          position: absolute;
          width: @icon-size;
          height: @icon-size;
          // top: 10px;
          left: 11px;
          top: 9px;
        }
        .text-container {
          position: absolute;
          left: 70px;
          top: 10px;
          div:first-child {
            font-size: 1.1rem;
            color: #6d8fa4;
          }
          div:last-child {
            font-size: 1.3rem;
            font-weight: bolder;
            color: #b4e4ff;
          }
        }
        // .text2 {
        //   right: -70px;
        // }
        // .text3 {
        //   right: -95px;
        // }
      }
    }
  }
  .box-title {
    border: 3px solid #1878d1;
    border-left: 0;
    border-bottom-right-radius: 20px;
    border-top-right-radius: 20px;
    height: 32px;
    line-height: 32px;
    width: 150px;
    font-size: 1.1rem;
    font-weight: bold;
    margin: 10px 20px 15px 10px;
  }
  .box-progress {
    display: flex;
    align-items: center;
    span {
      margin: 0 0 0 10px;
    }
  }
  // 右侧
  .content-right {
    // 上部分的高度
    @right-top: 465px;
    position: relative;
    .right-top {
      position: absolute;
      top: 1px;
      width: 97%;
      height: 458px;
      .box-title {
        // transform: translateY(55px);
        position: absolute;
        z-index: 1;
      }
      // 当前机组定位地图的样式
      .location-box {
        height: 75%;
        width: 75%;
        transform: translate(15%, 15%);
      }
      // 切换按钮
      .change-btn {
        position: absolute;
        top: 13px;
        z-index: 1;
        right: 0;
        background-color: transparent;
        color: white;
        border-color: #1878d1;
        &:hover {
          background-color: #1acdfd65;
        }
      }
    }
    .line-horizontal {
      height: 0;
      width: 100%;
      position: absolute;
      top: 440px;
    }
    .right-bottom {
      position: absolute;
      width: 100%;
      height: 440px;
      top: @right-top;
      transform: translateY(-35px);
      .box-title {
        transform: translateY(30px);
      }
    }
  }
}
</style>



