<!--
 * @Author: Demon3443002624@qq.com
 * @Date: 2021-11-17 11:30:45
 * @Description: 油耗历史记录列表数据
-->
<template>
  <div id="Tabledialog">
    <!-- Table -->
    <el-button type="text"
               @click="getTableData">历史记录>></el-button>
    <el-dialog title="机油油耗历史记录"
               :center="true"
               :modal-append-to-body="false"
               :visible.sync="dialogTableVisible">
      <div class="researchbox">
        <el-row type="flex"
                align="bottom">
          <el-col :span="20">
            <el-row>
              <el-col :span="6"
                      :pull="2"
                      :offset="2">
                <!-- <el-input v-model="input" placeholder="选择开机时间"></el-input> -->
                <el-date-picker v-model="starttime"
                                type="datetime"
                                placeholder="选择开机时间"
                                placement="bottom-start"
                                :clearable="false">
                </el-date-picker>
              </el-col>
              <el-col :span="6"
                      :pull="2"
                      :offset="2">
                <!-- <el-input v-model="input" placeholder="选择关机时间"></el-input> -->
                <el-date-picker v-model="endtime"
                                type="datetime"
                                placeholder="选择关机时间"
                                placement="bottom-start"
                                :clearable="false">
                </el-date-picker>
              </el-col>
              <el-col :span="6"
                      :pull="1"
                      :offset="1">
                <el-button @click="query"
                           type="text">点击查询</el-button>
              </el-col>
            </el-row>
          </el-col>
          <el-col :span="4"
                  class="checkout">
            <el-button @click="exportExcel"
                       type="primary">导出</el-button>
          </el-col>
        </el-row>
      </div>
      <div class="total-box">
        <span>【总计】总运行时长：{{
            Number(totalData.allTime).toFixed(2)
          }}（小时）；</span>
        <span>总发电量：{{
            Number(totalData.allProduct).toFixed(2)
          }}（KW·h）；</span>
        <span>总油耗：{{ Number(totalData.allFuel).toFixed(2) }}（L）；</span>
        <span>每KW·h油耗：{{
            !totalData.allFuelEveryProd
              ? ``
              : Number(totalData.allFuelEveryProd).toFixed(2) + `（L/KW·h）`
          }}</span>
      </div>
      <div>
        <!-- 表格 -->
        <el-table :data="
            tableData.slice(
              (currentPage - 1) * pageSize,
              currentPage * pageSize
            )
          "
                  size="medium"
                  style="width: 100%"
                  :highlight-current-row="false">
          <el-table-column v-for="value in columndata"
                           :width="value.width"
                           :label="value.label"
                           :key="value.key"
                           :prop="value.prop">
          </el-table-column>
        </el-table>
        <!-- 分页器 -->
        <div class="pagination-box mt15">
          <el-pagination align="center"
                         @size-change="handleSizeChange"
                         @current-change="handleCurrentChange"
                         :current-page="currentPage"
                         :page-sizes="[1, 5, 10]"
                         :page-size="pageSize"
                         layout="total, sizes, prev, pager, next, jumper"
                         :total="tableData.length">
          </el-pagination>
        </div>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import data from '../../data'

export default {
  data () {
    return {
      tableData: [],
      // tableData: data.historyList.tableData,
      currentPage: 1,
      total: data.historyList.tableData.length,
      // 总计数据
      totalData: {},
      pageSize: 5,
      dialogTableVisible: false,
      columndata: data.historyList.column,
      // 开始时间 默认本月
      starttime: this.$moment().add(-1, 'months').format('YYYY-MM-DD HH:mm'),
      // 结束时间
      endtime: this.$moment().format('YYYY-MM-DD HH:mm')
    }
  },
  props: {
    mchCode: {
      type: String
    }
  },
  watch: {
    starttime (newVal) {
      this.starttime = this.$moment(newVal).format('YYYY-MM-DD HH:mm')
    },
    endtime (newVal) {
      this.endtime = this.$moment(newVal).format('YYYY-MM-DD HH:mm')
    }
  },
  methods: {
    // 导出运行记录
    exportExcel () {
      const params = {
        mchCode: this.mchCode,
        starttime: this.starttime,
        endtime: this.endtime
      }

      this.$api.screenData.GensetStartStopRecordExportV2(params).then((res) => {
        let url = window.URL.createObjectURL(new Blob([res.data]))
        let link = document.createElement('a')
        link.style.display = 'none'
        link.href = url
        link.download =
          decodeURI(res.headers['content-disposition'].split('filename=')[1]) ||
          '未命名文件.xls'
        document.body.appendChild(link)
        link.click()
      })
    },
    // 每页条数改变时触发 选择一页显示多少行
    handleSizeChange (val) {
      this.currentPage = 1
      this.pageSize = val
    },
    // 当前页改变时触发 跳转其他页
    handleCurrentChange (val) {
      this.currentPage = val
    },
    // 点击查询
    query (e) {
      const flag = this.$moment(this.endtime).diff(this.starttime)
      if (flag < 0) {
        this.$message.error('提示：结束时间应该在开始时间之后！')
      }
      const loading = this.$loading({
        lock: true,
        text: '数据加载中，请勿操作...',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      // 先清空列表数据
      this.tableData = []
      this.setTableData(
        {
          StartTime: this.starttime,
          EndTime: this.endtime
        },
        (res) => {
          loading.close()
        }
      )
    },
    setTableData (para, cb) {
      const params = {
        StartTime: this.starttime,
        EndTime: this.endtime
      }
      const urlparams = {
        id: this.mchCode
      }
      this.$api.screenData
        .FindGeneratorStartStopRecords(params, urlparams)
        .then((resOriginal) => {
          try {
            const res = resOriginal.Data
            const datalist = res.map((item, index) => {
              const endtime = this.$moment(
                new Date(
                  Number(item.EndTime.replace(`/Date(`, '').replace(`)/`, ''))
                )
              ).format(`YYYY-MM-DD HH:mm`)
              const starttime = this.$moment(
                new Date(
                  Number(item.StartTime.replace(`/Date(`, '').replace(`)/`, ''))
                )
              ).format(`YYYY-MM-DD HH:mm`)
              const singleproduct = Number(item.EndKwh) - Number(item.StartKwh)
              const singlefuel =
                Number(item.EndOilLevel) - Number(item.StartOilLevel)
              // 保留小数点后几位
              const fixednum = 2
              const obj = {
                order: index + 1,
                opentime: starttime,
                showdowntime: endtime,
                // runtime: (
                //   this.$moment(endtime).diff(this.$moment(starttime)) / 36e5
                // ).toFixed(fixednum),
                runtime: item.Remark,
                singleproduct: singleproduct.toFixed(fixednum),
                singlefuel: singlefuel.toFixed(fixednum),
                singlefueleverykwh: singleproduct
                  ? (singlefuel / singleproduct).toFixed(0)
                  : ''
              }
              return obj
            })
            const totalData = {
              allTime: 0,
              allProduct: 0,
              allFuel: 0,
              allFuelEveryProd: 0
            }
            datalist.forEach((e) => {
              totalData.allTime += Number(e.runtime)
              totalData.allProduct += Number(e.singleproduct)
              totalData.allFuel += Number(e.singlefuel)
            })
            totalData.allFuelEveryProd =
              totalData.allProduct === 0
                ? ``
                : totalData.allFuel / totalData.allProduct
            this.totalData = totalData
            this.tableData = datalist
          } catch (err) { }
          cb(resOriginal)
        })
        .catch((err) => { })
    },
    getTableData () {
      this.dialogTableVisible = true
      const loading = this.$loading({
        lock: true,
        text: '数据加载中，请勿操作...',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      // 先清空列表数据
      this.tableData = []
      this.setTableData(undefined, (res) => {
        loading.close()
      })
    }
  }
}
</script>
<style lang="less" scoped>
#Tabledialog {
  // table 的左右间距
  @sideinterval: 40px;
  // table 透明度（影响了文字）
  @tableopacity: 0.95;
  .researchbox {
    height: 70px;
    background: #08192e;
    opacity: @tableopacity;
    width: 100%;
    padding: 7px;
    > div {
      height: 100%;
    }
    .checkout {
      display: flex;
      justify-content: end;
    }
  }
  .total-box {
    height: 40px;
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: center;
    font-size: 1.2rem;
    font-weight: 600;
    color: #8fc2e3;
  }
}
</style>
<style lang="less">
// table 的左右间距
@sideinterval: 40px;
// table 透明度（影响了文字）
@tableopacity: 0.95;
#Tabledialog {
  // 重写 Element UI 的 input 框的样式
  .el-input__inner {
    background-color: #092341;
    border: 2px solid #063c7e;
    color: white;
  }
  // 重写 Element UI 的 dialog 及 table 样式
  .el-dialog,
  .el-pager li {
    background-color: transparent;
  }
  .el-dialog .el-dialog__header {
    background-color: #1f4b7e;
    opacity: 0.8;
    .el-dialog__title,
    i {
      color: white;
    }
  }
  .el-dialog--center .el-dialog__body {
    text-align: initial;
    padding: 0 @sideinterval 30px @sideinterval;
    background: #08192e;
    opacity: @tableopacity;
  }
  .el-table th {
    /* background: #D8E7FF !important; */
    background: #094f94 !important;
    color: #b4e4ff;
  }
  .el-table tr {
    background-color: #094f94;
    color: #b4e4ff;
  }
  //
  .el-table,
  .el-table__expanded-cell {
    background-color: transparent;
  }
  .el-table__row:hover {
    // background-color: red;
  }
  // 修改行选中的颜色
  .el-table__row:hover td {
    background-color: #3b6fdb !important;
  }
  //
  .el-pagination button {
    background-color: #08192e;
  }
}
</style>



