<!--
 * @Author: lpeng
 * @Date: 2021-11-17 11:30:45
 * @Description: 油耗历史记录列表数据
-->
<template>
  <div class="TabledialogWarning">
    <el-button type="text" @click="clickHandler"></el-button>

    <el-dialog
      title="提示信息"
      :center="true"
      :visible.sync="dialogTableVisible"
      :modal-append-to-body="false"
    >
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="当前报警" name="first">
          <div>
            <!-- 表格 -->
            <el-table
              :data="warningdatas"
              size="medium"
              style="width: 100%"
              height="500px"
              :highlight-current-row="false"
            >
              <el-table-column
                v-for="value in waringcolumn"
                :width="value.width"
                :label="value.label"
                :key="value.key"
                align="left"
                :prop="value.prop"
              >
              </el-table-column>
              <el-table-column width="4" label="操作" key="title8" align="center">
                <template slot-scope="scope">
                  <el-button @click="handleClick_check(scope.row)" type="text" size="medium"
                    >查看</el-button
                  >
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-tab-pane>
        <el-tab-pane label="保养信息" name="second">
          <div class="">
            <!-- 表格 -->
            <el-table
              :data="keepdatas"
              size="medium"
              style="width: 100%"
              height="500px"
              :highlight-current-row="false"
            >
              <el-table-column
                v-for="value in keepcolumn"
                :width="value.width"
                :label="value.label"
                :key="value.key"
                :prop="value.prop"
                align="left"
              >
              </el-table-column>
              <el-table-column width="2" label="操作" key="title6" align="center">
                <template slot-scope="scope">
                  <el-button @click="handleClick_check(scope.row)" type="text" size="medium"
                    >查看</el-button
                  >
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-tab-pane>
      </el-tabs>
    </el-dialog>
  </div>
</template>

<script>
import data from '../../data'
import { formatTime } from '@/utils/common'

export default {
  data() {
    return {
      activeName: 'first',
      dialogTableVisible: false,
      warningdatas: [],
      waringcolumn: data.warninglist.waringcolumn,
      keepdatas: [],
      // keepdatas: [],
      keepcolumn: data.warninglist.keepcolumn,
      // 实时报警数据
      warningDataTable: [],
      // 保养数据
      keepDataTable: []
    }
  },
  watch: {
    warningDataTable(val, old) {
      this.warningdatas = val
    },
    keepDataTable(val, old) {
      this.keepdatas = val
      // ElementUI 有个 bug 列表表头总是多列单元格，此处将其删除
      const el1 = document.querySelectorAll(
        `.TabledialogWarning  thead>tr>th:last-child[class*='gutter']`
      )
      const el2 = document.querySelectorAll(
        `.TabledialogWarning  colgroup>col:last-child[name='gutter']`
      )
      el1.forEach((e) => {
        e.remove()
      })
      el2.forEach((e) => {
        e.remove()
      })
    }
  },
  mounted() {},
  methods: {
    handleClick(tab, event) {},
    handleClick_check(param) {
      // flag true 为点击的保养列表
      const flag = param.unit
      // 跳转到保养方案页面
      flag && this.$router.push('/unit-maintenance-manage/maintain-programme')
      flag || this.$emit('changeMchID', { mchId: param.GeneratorSetId })
    },
    clickHandler() {
      this.dialogTableVisible = true
      // 清空列表数据，以重载
      this.warningdatas = []
      this.keepdatas = []
      const loading = this.$loading({
        lock: true,
        text: '数据加载中，请勿操作...',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      this.setWarnKeepData((res) => {
        loading.close()
      })
    },
    setWarnKeepData(cb) {
      this.$api.screenData.FindGeneratorSetRadioIndexPage().then((resOriginal) => {
        if (resOriginal) {
          const res = resOriginal.Data
          const alarmList = res.AlarmList || []
          const remindList = res.RemindList || []
          let warningData = new Array()
          let keepData = new Array()
          alarmList.forEach((e, i) => {
            const obj = {
              order: i + 1,
              time: formatTime(e.Time).format('yyyy-MM-DD HH:mm:ss'),
              mchname: e.GensetName,
              divisionmch: '/',
              warningcontent: e.Content,
              warningtype: `警告`,
              // warningtype: e.Type,
              operation: '查看',
              // 机组ID
              GeneratorSetId: e.GeneratorSetId
            }
            warningData.push(obj)
          })
          remindList.forEach((e, i) => {
            const obj = {
              order: i,
              unit: e.GensetName,
              plandate: formatTime(e.Time).format('YYYY-MM-DD'),
              type: `保养`,
              // type: e.Type,
              keepContent: e.Content,
              operation: '查看'
            }
            keepData.push(obj)
          })
          this.warningdatas = [
            ...warningData.map((item) => '实时报警：' + item.mchname + '-' + item.warningcontent),
            ...keepData.map((item) => '保养提醒：' + item.unit + item.keepContent)
          ]
          // 排序最新的在最前面
          warningData.sort((a, b) => {
            const res = this.$moment(a.time).diff(this.$moment(b.time))
            return -res
          })
          keepData.sort((a, b) => {
            const res = this.$moment(a.plandate).diff(this.$moment(b.plandate))
            return -res
          })
          keepData = keepData.map((e, index) => {
            e.order = index + 1
            return e
          })
          warningData = warningData.map((e, index) => {
            e.order = index + 1
            return e
          })
          this.warningDataTable = warningData
          this.keepDataTable = keepData
        }
        cb(resOriginal)
      })
    }
  }
}
</script>
<style lang="less">
// 样式覆盖
.TabledialogWarning {
  // table 的左右间距
  @sideinterval: 40px;
  // table 透明度（影响了文字）
  @tableopacity: 0.95;
  .el-button {
    width: 100%;
  }
  .el-dialog--center .el-dialog__body {
    padding: 30px 40px !important;
  }
  // 重写 tab 标签
  .el-tabs__item {
    background-color: white;
    columns: gray;
    padding: 0 20px !important;
  }
  .el-tabs__item[class*='is-active'] {
    background-color: rgba(9, 79, 148);
    columns: white;
    padding: 0 20px !important;
  }
  .el-tabs__nav-wrap::after {
    background-color: transparent;
  }
  // 重写 Element UI 的 dialog 及 table 样式
  .el-dialog,
  .el-pager li {
    background-color: transparent;
  }
  .el-dialog .el-dialog__header {
    background-color: #1f4b7e;
    opacity: 0.8;
    .el-dialog__title,
    i {
      color: white;
    }
  }
  .el-dialog--center .el-dialog__body {
    text-align: initial;
    padding: 0 @sideinterval 30px @sideinterval;
    background: #08192e;
    opacity: @tableopacity;
  }
  .el-table th {
    /* background: #D8E7FF !important; */
    background: #094f94 !important;
    color: #b4e4ff;
  }
  .el-table tr {
    background-color: #094f94;
    color: #b4e4ff;
  }
  .el-table,
  .el-table__expanded-cell {
    background-color: transparent;
  }
  // 修改 tab 选项按钮样式
  .el-tabs__item {
    background-color: transparent;
    color: white;
  }
  // 修改行选中的颜色
  .el-table__row:hover td {
    background-color: #3b6fdb !important;
  }
}
</style>



