
<template>
  <div class="round">
    <div class="line1"></div>
    <div class="line2"></div>
    <div class="line3"></div>
    <div class="dashed-round"></div>
  </div>
</template>

<script>
export default {
  name: 'IconRotating'
}
</script>

<style lang="less" scoped>
// 外圈缺口的宽度
@round-gap: 6px;
@round-width: 60px;
@round-interval: 2px;
@round-width-inner:@round-width - 3 * @round-interval;
.round {
  position: relative;
  width: @round-width;
  height: @round-width;
  background: #07233435;
  border-radius: @round-width;
  border: 4px solid #1acdfd;
  animation: rotate2 30s linear infinite;

  .line {
    height: 12px;
    width: @round-gap;
    position: absolute;
    background: #041121;
    // background: #fff;
  }
  .line1 {
    .line();
    transform: rotate(-60deg);
  }
  .line2 {
    .line();
    transform: rotate(60deg);
    right: 0;
    top: 0;
  }
  .line3 {
    .line();
    right: calc(50% - @round-gap / 2);
    bottom: -10px;
  }
  .dashed-round {
    position: absolute;
    top: @round-interval;
    left: @round-interval;
    // border: 1px dashed #000;
    width: @round-width-inner;
    height: @round-width-inner;
    border: 1px dashed #d9c4c488;
    border-radius: @round-width-inner;
    animation: rotate1 15s linear infinite;
  }
  // 旋转动画，逆时针
  @keyframes rotate1 {
    0% {
      transform: rotate(0deg);
    }
    25% {
      transform: rotate(-90deg);
    }
    50% {
      transform: rotate(-180deg);
    }
    75% {
      transform: rotate(-270deg);
    }
    100% {
      transform: rotate(-360deg);
    }
  }
  @keyframes rotate2 {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
}
</style>



