<!--
 * @Author: Demon3443002624@qq.com
 * @Date: 2021-11-12 09:07:43
 * @Description: 数字大屏中间的旋转动态展示图
-->
<template>
  <div id="FuelComponent">
    <div class="curve bgleft">
      <img src="@/assets/img/screenData/left1.svg" />
    </div>
    <div class="middle">
      <div class="backbg opacity bg1">
        <img src="@/assets/img/screenData/bg1.svg" />
      </div>
      <div class="backbg opacity2 bg2">
        <img src="@/assets/img/screenData/bg2.svg" />
      </div>
      <div class="backbg bg3">
        <img src="@/assets/img/screenData/bg3.svg" />
      </div>
      <div class="backbg bg4">
        <img src="@/assets/img/screenData/bg4.svg" />
      </div>
      <div class="backbg bg5">
        <img src="@/assets/img/screenData/bg5.svg" />
      </div>
      <div class="backbg bg6">
        <img src="@/assets/img/screenData/bg6.svg" />
      </div>
      <div class="backbg text-box singlemch" :title="displayName">
        <div class="mchname">{{ displayName }}</div>
        <div>智能动力装备</div>
        <div :class="`normal${stateColor[status]}`">{{ status }}</div>
      </div>
      <!-- 20211207周二15:48:54lpeng：需动态更改，暂无数据，后面改 -->
      <!-- <div class="backbg text-box mergemch">
        <el-carousel
          height="100%"
          arrow="always"
          :autoplay="false"
          :loop="true"
          ref="myCarousel"
          @change="changeSubMch"
        >
          <el-carousel-item
            v-for="item in mergemchdata"
            :key="item.divisionmch"
          >
            <div class="container">
              <div>{{ item.displayName }}</div>
              <div>智能动力装备</div>
              <div>({{ item.divisionmch }})</div>
              <div :class="`normal${stateColor[item.status]}`">
                {{ item.status }}
              </div>
            </div>
          </el-carousel-item>
        </el-carousel>
        <svg-icon
          icon-class="leftIcon"
          class="icon-left"
          @click="changeCarousel(true)"
        ></svg-icon>
        <svg-icon
          icon-class="rightIcon"
          class="icon-right"
          @click="changeCarousel(false)"
        ></svg-icon>
      </div> -->
    </div>
    <div class="curve right">
      <img src="@/assets/img/screenData/right1.svg" />
    </div>
  </div>
</template>

<script>
import data from '../../data'
export default {
  name: 'FuelComponent',
  props: {
    displayName: {
      type: String,
      default: 'FPSS7821100835'
    },
    status: {
      type: String,
      default: '正常运行'
    }
  },
  data() {
    return {
      mergemchdata: data.mergemchdata.map((item) => {
        const l = 14
        if (item.displayName.length > l) {
          item.displayName = item.displayName.slice(0, l) + `...`
        }
        return item
      }),
      stateColor: {
        离线: '0',
        停机: '1',
        警告: '2',
        报警: '3',
        运行: '4'
      }
    }
  },
  mounted() {
    const arr = ['leftIcon', 'rightIcon']
    arr.forEach((item, index) => {
      const btn = document.querySelectorAll(`#FuelComponent button`)[0]
      btn && btn.remove()
    })
  },
  methods: {
    changeCarousel(direction) {
      direction ? this.$refs.myCarousel.prev() : this.$refs.myCarousel.next()
    },
    changeSubMch(newVal, oldVal) {
      this.$emit(`changeSubMch`, newVal, oldVal, this.mergemchdata[newVal].divisionmchId)
    }
  }
}
</script>
<style lang="less" scoped>
@containerWidth: 480px;
// 圆圈与圆圈背景的间隔
@circleInterval: 40px;
@circleTransform: calc(@circleInterval / 2);
#FuelComponent {
  position: relative;
  .bgleft {
    position: absolute;
    transform: translateX(-184px) translateY(54px) rotateZ(54deg) scale(0.8);
  }
  .right {
    position: absolute;
    transform: translateX(176px) translateY(52px) rotateZ(-55deg) scale(0.8);
  }
  .curve {
    opacity: 0.7;
    // 此处因将走马灯的指示器遮挡了，添加 z-index
    z-index: -1;
  }

  .mchname {
    width: 90%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    text-align: center;
  }
  .middle {
    // margin: 0 0 0 40px;
    width: @containerWidth;
    position: relative;
    // margin: 70px 0 0 70px;
    .backbg {
      position: absolute;
      top: 0;
      left: 0;
    }
    .opacity {
      opacity: 0.6;
    }
    .opacity2 {
      opacity: 0.45;
    }
    .bg1 {
      img {
        width: calc(@containerWidth);
      }
    }
    .bg2 {
      img {
        width: calc(@containerWidth - @circleInterval);
      }
      transform: translateX(@circleTransform) translateY(@circleTransform);
    }
    .bg3 {
      img {
        width: calc(@containerWidth - 2 * @circleInterval);
      }
      transform: translateX(calc(@circleTransform*2)) translateY(calc(@circleTransform*2));
    }
    .bg4 {
      img {
        width: calc(@containerWidth - 3 * @circleInterval);
      }
      top: calc(3 * @circleTransform);
      left: calc(3 * @circleTransform);
      animation: bg4 20s linear infinite;
    }
    .bg5 {
      img {
        width: calc(@containerWidth - 5 * @circleInterval);
      }
      top: calc(5 * @circleTransform);
      left: calc(5 * @circleTransform);
      animation: bg5 30s linear infinite;
    }
    .bg6 {
      img {
        width: calc(@containerWidth - 6 * @circleInterval);
      }
      transform: translateX(calc(@circleTransform*6)) translateY(calc(@circleTransform*6));
    }
    .text-box {
      width: calc(@containerWidth - 6 * @circleInterval);
      height: calc(@containerWidth - 6 * @circleInterval);
      transform: translateX(calc(@circleTransform*6)) translateY(calc(@circleTransform*6));
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      font-size: 1.5rem;
      font-weight: 600;
      opacity: 0.8;
      font-family: 'Helvetica Neue', Helvetica, 'PingFang SC', 'Hiragino Sans GB', 'Microsoft YaHei',
        '微软雅黑', Arial, sans-serif;
    }
    .mergemch {
      .container {
        display: flex;
        flex-direction: column;
        align-items: center;
        transform: translateY(70px);
      }
      // 多机组左右指示器的大小
      .svg-icon {
        font-size: 45px;
        transform: translateY(-10px);
        cursor: pointer;
      }
      .icon-left {
        position: absolute;
        left: 0;
      }
      .icon-right {
        position: absolute;
        right: 0;
      }
    }
    // 旋转动画（无限播放）
    @keyframes bg4 {
      0% {
        transform: rotate(0deg);
      }
      25% {
        transform: rotate(-90deg);
      }
      50% {
        transform: rotate(-180deg);
      }
      75% {
        transform: rotate(-270deg);
      }
      100% {
        transform: rotate(-360deg);
      }
    }
    @keyframes bg5 {
      0% {
        transform: rotate(0deg);
      }
      100% {
        transform: rotate(360deg);
      }
    }
    @color-warning: #f2c40d;
    @color-attention: #fd2e2d;
    .normal0 {
      color: #808080;
    }
    .normal1 {
      color: #f6f6f6;
    }
    .normal2 {
      color: @color-warning;
      animation: blinkWarn 1s linear infinite;
    }
    .normal3 {
      animation: blink 1s linear infinite;
      color: @color-attention;
    }
    .normal4 {
      color: #20be62;
    }
    // 文字闪烁动画 报警和警告
    @keyframes blinkWarn {
      0% {
        color: @color-warning;
      }

      50% {
        color: #f2c40d41;
      }

      100% {
        color: @color-warning;
      }
    }
    @keyframes blink {
      0% {
        color: @color-attention;
      }

      50% {
        color: #fd2d2d3a;
      }

      100% {
        color: @color-attention;
      }
    }
    // 多机组中部样式
    .el-carousel--horizontal {
      height: 100%;
      width: 100%;
    }
    // 隐藏走马灯的指示条
    // .el-carousel__indicators {
    /deep/ .el-carousel__indicators {
      display: none;
    }
    // 指示器被走马灯遮挡了
    /deep/ .el-carousel__container {
      z-index: -1;
    }
  }
}
</style>
<style lang='less'>
// 隐藏走马灯的指示条
.el-carousel__indicators {
  // display: none;
}
</style>



