<!--
 * @Author: Demon3443002624@qq.com
 * @Date: 2021-11-16 11:30:30
 * @Description: 数字大屏的地图组件（需在 main.js 中引入 china.js
-->
<template>
  <div class="fuelcurve-component"></div>
</template>
<script>
export default {
  data () {
    return {
      // echarts 图表实例
      chart: null
    }
  },
  props: {
    fuelcurve: {
      type: Object
    }
  },
  watch: {
    fuelcurve (val, old) {
      // 监听到 prop 改变，重新渲染如表
      this.renderChart()
    }
  },
  methods: {
    renderChart () {
      const el = document.querySelector('.fuelcurve-component')
      if (!this.chart) this.chart = this.$echarts.init(el)
      const option = {
        grid: {
          right: 20
        },
        xAxis: {
          type: 'time',
          boundaryGap: [0, '20%'],
          // data: this.fuelcurve.x,
          splitNumber: 6,
          // interval: 1000 * 60 * 5,
          // maxInterval: 1000 * 60 * 20,
          // minInterval: 1000 * 60 * 1,
          axisPointer: {
            show: true,
            type: 'line',
            snap: true,
            lineStyle: {
              color: '#17bae9',
              type: 'solid'
              // width: 2,
            },
            label: {
              show: false
            }
          },
          axisLine: {
            lineStyle: {
              color: 'white',
              opacity: 0.5
            }
          },
          axisTick: {
            show: true
          }
        },
        yAxis: {
          type: 'value',
          axisTick: {
            show: true
          },
          axisLine: {
            lineStyle: {
              color: 'white',
              opacity: 0.5
            }
          },
          interval: 10,
          splitLine: {
            lineStyle: {
              color: '#0e182c',
              opacity: 0.7
            }
          },
          splitArea: {
            show: true,
            areaStyle: {
              color: ['#03132b', '#061f41']
            }
          }
        },
        tooltip: {
          trigger: 'axis',
          show: true,
          position: 'top',
          backgroundColor: 'rgba(2,20,4,0.5)',
          formatter: (params, ticket, cb) => {
            const value = params[0].value
            return `
                <div style="color:#1acdfd">
                <ul>
                  <li>${value[0]}</li>
                  <li>剩余油量：${value[2]}</li>
                  <li>剩余占比：${value[1]}</li>
                </ul>
                </div>
                `
          }
        },
        series: [
          {
            data: this.fuelcurve.data,
            type: 'line',
            smooth: true,
            connectNulls: false,
            areaStyle: {
              // color: "#134681",
              color: {
                type: 'linear',
                x: 0,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [
                  {
                    offset: 0,
                    color: '#108e9b' // 0% 处的颜色
                  },
                  {
                    offset: 1,
                    color: '#16347e' // 100% 处的颜色
                  }
                ],
                global: false // 缺省为 false
              },
              opacity: 0.6
            },
            // symbol: "emptyCircle",
            symbol: (value, params) => {
              return 'emptyCircle'
            },
            symbolSize: 7,
            showSymbol: false,
            emphais: {
              scale: true
            },
            markPoint: {
              symbol: 'pin'
            }
          }
        ]
      }
      this.chart.setOption(option)
    }
  },
  mounted () {
    this.renderChart()
  }
}
</script>
<style scoped>
.fuelcurve-component {
  height: 100%;
  width: 100%;
}
</style>



