<!--
 * @Author: Demon3443002624@qq.com
 * @Date: 2021-11-16 11:30:30
 * @Description: 数字大屏的地图组件
-->
<template>
  <div class="map-component">
    <div class="map-container"
         ref="myEchart"></div>
  </div>
</template>
<script>
// 百度地图开发 sdk key
import loadBMap from '@/utils/loadBMap.js'
export default {
  data () {
    return {
      // 地图标点根据机组状态显示不同的颜色
      stateColor: {
        报警: '#fd2e2d',
        警告: '#f2c40d',
        运行: '#20be62',
        停机: '#f6f6f6',
        离线: '#808080'
        // 离线: "#20be62",
      }
    }
  },
  mounted () {
    // this.renderMap();
  },
  props: {
    // 地图数据
    mapdata: {
      type: Object,
      default: _ => { }
    },
    documentWidth: {
      type: Number
    }
  },
  watch: {
    mapdata (newVal, oldVal) {
      this.renderMap()
    },
    documentWidth (n, o) {
      this.renderMap()
    }
  },
  methods: {
    renderMap () {
      const mapdata = this.mapdata
      loadBMap('o7gALXwtch6SNk9jjH4DmFffcFt1PhFL')
        .then(() => {
          // 百度地图API功能
          this.myEchart ||
            (this.myEchart = this.$echarts.init(this.$refs.myEchart, null, {
              renderer: 'svg'
            }))
          let data = [{ name: '海门', value: 9 }]
          let convertData = function (data) {
            // let res = [];
            // for (let i = 0; i < data.length; i++) {
            //   let geoCoord = geoCoordMap[data[i].name];
            //   if (geoCoord) {
            //     res.push({
            //       name: data[i].name,
            //       value: geoCoord.concat(data[i].value),
            //     });
            //   }
            // }
            //
            // return res;
            const geoCoordMap = mapdata || []
            const resdata = []
            for (const key in geoCoordMap) {
              resdata.push({
                name: key,
                value: geoCoordMap[key]
              })
            }
            return resdata
          }
          let option = {
            backgroundColor: 'transparent',
            title: {
              text: '',
              subtext: '',
              sublink: '',
              left: 'center',
              textStyle: {
                color: '#fff'
              },
              show: false
            },
            tooltip: {
              trigger: 'item'
            },
            bmap: {
              center: [104.114129, 34.550339],
              // zoom: 5,
              zoom: 4,
              roam: true,
              mapStyle: {
                styleJson: [
                  {
                    featureType: 'water',
                    elementType: 'all',
                    stylers: {
                      color: '#044161'
                    }
                  },
                  {
                    featureType: 'land',
                    elementType: 'all',
                    stylers: {
                      color: '#011455'
                    }
                  },
                  {
                    featureType: 'boundary',
                    elementType: 'geometry',
                    stylers: {
                      color: '#064f85'
                    }
                  },
                  {
                    featureType: 'railway',
                    elementType: 'all',
                    stylers: {
                      visibility: 'off'
                    }
                  },
                  {
                    featureType: 'highway',
                    elementType: 'geometry',
                    stylers: {
                      color: '#011455'
                    }
                  },
                  {
                    featureType: 'highway',
                    elementType: 'geometry.fill',
                    stylers: {
                      color: '#005b96',
                      lightness: 1
                    }
                  },
                  {
                    featureType: 'highway',
                    elementType: 'labels',
                    stylers: {
                      visibility: 'off'
                    }
                  },
                  {
                    featureType: 'arterial',
                    elementType: 'geometry',
                    stylers: {
                      color: '#011455'
                    }
                  },
                  {
                    featureType: 'arterial',
                    elementType: 'geometry.fill',
                    stylers: {
                      color: '#00508b'
                    }
                  },
                  {
                    featureType: 'poi',
                    elementType: 'all',
                    stylers: {
                      visibility: 'off'
                    }
                  },
                  {
                    featureType: 'green',
                    elementType: 'all',
                    stylers: {
                      color: '#056197',
                      visibility: 'off'
                    }
                  },
                  {
                    featureType: 'subway',
                    elementType: 'all',
                    stylers: {
                      visibility: 'off'
                    }
                  },
                  {
                    featureType: 'manmade',
                    elementType: 'all',
                    stylers: {
                      visibility: 'off'
                    }
                  },
                  {
                    featureType: 'local',
                    elementType: 'all',
                    stylers: {
                      visibility: 'off'
                    }
                  },
                  {
                    featureType: 'arterial',
                    elementType: 'labels',
                    stylers: {
                      visibility: 'off'
                    }
                  },
                  {
                    featureType: 'boundary',
                    elementType: 'geometry.fill',
                    stylers: {
                      color: '#029fd4'
                    }
                  },
                  {
                    featureType: 'building',
                    elementType: 'all',
                    stylers: {
                      color: '#1a5787'
                    }
                  },
                  // 字体填充
                  {
                    featureType: 'label',
                    elementType: 'labels.text.fill',
                    stylers: {
                      visibility: 'on',
                      color: '#2dc4bbff',
                      weight: '0'
                    }
                    // stylers: {
                    //   color: "#999999",
                    // },
                  },
                  // 字体描边
                  {
                    featureType: 'label',
                    // elementType: "labels.text.fill",
                    elementType: 'labels.text.stroke',
                    stylers: {
                      visibility: 'on',
                      color: '#ffffff00',
                      weight: '1'
                    }
                    // stylers: {
                    //   color: "#999999",
                    // },
                  },
                  // 不展示城市前的图标
                  {
                    featureType: 'city',
                    elementType: 'labels.icon',
                    stylers: {
                      visibility: 'off'
                    }
                  }
                ]
              }
            },
            series: [
              // {
              //   name: "pm2.5",
              //   type: "scatter",
              //   coordinateSystem: "bmap",
              //   data: convertData(data),
              //   encode: {
              //     value: 2,
              //   },
              //   symbolSize: function (val) {
              //     return val[2] / 10;
              //   },
              //   label: {
              //     formatter: "{b}",
              //     position: "right",
              //   },
              //   itemStyle: {
              //     color: "#ddb926",
              //   },
              //   emphasis: {
              //     label: {
              //       show: true,
              //     },
              //   },
              // },
              {
                name: '',
                type: 'effectScatter',
                coordinateSystem: 'bmap',
                data: convertData(data),
                // 注意：series.tooltip 仅在 tooltip.trigger 为 'item' 时有效。
                tooltip: {
                  position: 'top',
                  // position: function (point, params, dom, rect, size) {
                  //   // 固定在顶部
                  //   return [point[0], "10%"];
                  // },
                  show: true,
                  formatter: (params, ticket, cb) => {
                    const item = params.data
                    const value = item.value
                    return `
                <div style="color:#b4e4ff">
                  <div>${item.name}(${value[4]})</div>
                  <div>${value[0]}</div>
                  <div>${value[1]}</div>
                  <div>${value[2]}</div>
                </div>
                `
                  },
                  backgroundColor: 'rgba(2,20,4,0.9)'
                },
                encode: {
                  value: 2
                },
                // 高亮显示标签
                emphasis: {
                  label: {
                    show: false
                  }
                },
                symbolSize: function (val) {
                  // return val[2] / 10;
                  return 10
                },
                showEffectOn: 'emphasis',
                rippleEffect: {
                  brushType: 'stroke'
                },
                label: {
                  formatter: '{b}',
                  position: 'right',
                  show: false
                },
                itemStyle: {
                  // color: "red",
                  color: (params) => {
                    const data = params.data
                    if (data && data.value) {
                      return this.stateColor[data.value[4]]
                    }
                    return this.stateColor['运行']
                  }
                  // shadowBlur: 10,
                  // shadowColor: "#333",
                },
                zlevel: 1
              }
              // {
              //   type: "custom",
              //   coordinateSystem: "bmap",
              //   renderItem: renderItem,
              //   itemStyle: {
              //     opacity: 0.5,
              //   },
              //   animation: false,
              //   silent: true,
              //   data: [0],
              //   z: -10,
              // },
            ]
          }
          this.myEchart.setOption(option)
          this.myEchart.on('click', (e) => {
            if (e.componentSubType === 'effectScatter') {
              const data = {
                displayName: e.value[3],
                status: e.value[4],
                mchCode: e.value[5]
              }
              this.$emit(`cb`, data)
            }
          })
          // 得到百度地图对象
          const bmap = this.myEchart.getModel().getComponent('bmap').getBMap()
          bmap.disableDoubleClickZoom()

          // bmap.addControl(new BMap.NavigationControl()); // 添加平移缩放控件
          // bmap.addControl(new BMap.ScaleControl()); // 添加比例尺控件
          // bmap.enableContinuousZoom(); //启用地图惯性拖拽，默认禁用
          // bmap.addControl(new BMap.OverviewMapControl()); //添加缩略地图控件
          // bmap.enableScrollWheelZoom(); //启用滚轮放大缩小
          // bmap.addControl(new BMap.MapTypeControl()); //添加地图类型控件
          // bmap.disable3DBuilding();
          // bmap.centerAndZoom(point, 14);
          // bmap.setMapStyle({ style: "normal" }); //设置模板的初始风格
        })
        .catch((err) => { })
    }
  }
}
</script>
<style lang="less" scoped>
.map-component {
  height: 94%;
  width: 100%;
  transform: translateY(4px);
  .map-container {
    height: 100%;
    width: 100%;
    /* transform: scale(1.1); */
  }
}
</style>
<style lang="less">
/* 隐藏文字信息 */
.anchorBL {
  display: none;
}
.ec-extension-bmap > div > div > div > div {
  // box-shadow: inset 0px 0px 20px 20px #00124c8f;
}
</style>



